@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
:root {
  --gray-500: #b8b6b4;
  --gray-600: #1B2838;
  --gray-800: #171a21;
}

* {
  font-family: "PT Sans", sans-serif;
}

html {
  background-color: var(--gray-800);
  font-size: 87.5%;
}
@media (min-width: 740px) {
  html {
    font-size: 93.75%;
  }
}

body {
  --webkit-font-smoothing: antialiased;
}

.container {
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1140px) {
  .container {
    max-width: 1140px;
  }
}

/*
    Este documento foi desenvolvido com base no mesmo contido em: https://meyerweb.com/eric/tools/css/reset/
 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.swiper {
  width: auto;
  height: 300px;
}

.swiper-button-prev:after, .swiper-rtl .swiper-button-next:after,
.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after {
  color: #fff;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  background-color: #fff;
}

:root {
  --background-top-header: var(--gray-600);
  --font-brand-color: var(--gray-500);
  --font-menu-color: var(--gray-500);
}

.header {
  align-items: center;
  background-color: var(--background-top-header);
  display: flex;
  padding: 2.35rem 0;
}
@media (min-width: 740px) {
  .header {
    height: 5rem;
    padding: 0;
  }
}
.header__top-bar {
  padding: 0 10px;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: 1fr;
  grid-row: 10px;
}
@media (min-width: 740px) {
  .header__top-bar {
    grid-template-columns: 1fr 2fr 1fr;
  }
}
.header__top-bar h1 {
  color: var(--font-brand-color);
  font-size: 1.2rem;
  text-align: center;
}
@media (min-width: 740px) {
  .header__top-bar h1 {
    text-align: left;
    font-size: 3.5rem;
  }
}
.header__top-bar-menu {
  align-items: center;
  display: flex;
  justify-content: center;
}
.header__top-bar-menu ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.header__top-bar-menu ul li {
  color: var(--font-menu-color);
  margin: 0 0.25rem;
}
.header__top-bar-icons {
  align-items: center;
  display: flex;
  justify-content: center;
}
@media (min-width: 740px) {
  .header__top-bar-icons {
    justify-content: flex-start;
  }
}
.header__top-bar-icons p {
  color: var(--font-menu-color);
}

:root {
  --background-circle: var(--gray-500);
}

.home__circles {
  display: flex;
  align-items: center;
  height: 12rem;
}
.home__circles-content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  gap: 0.25rem;
}
.home__circles-content-area {
  background-color: var(--background-circle);
  border-radius: 500px;
  height: 70px;
  width: 70px;
  text-align: center;
}